/**
 * Represents the supported resource types
 */
enum ResourceType {
    /**
     * Dispatcher resources
     */
    Dispatcher = 1,

    /**
     * Store resources
     */
    Store = 2,

    /**
     * Action Creator resources
     */
    ActionCreator = 3,

    /**
     * Executor resources
     */
    Executor = 4,

    /**
     * Data provider resources
     */
    DataProvider = 5,
}

export default ResourceType;
